body
{
    box-sizing: border-box;
    --color60: rgb(239, 254, 253);
    --color30: #ffffff;
    --color10: rgb(20, 193, 240);
    background-color: var(--color60);
    font-size: 36px;
    font-family:cursive;
}
.Reg
{
    width: 600px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
}
.Reg-form
{
    display: block;
    height: 500px;
    box-shadow: 1.5px 1.5px 5px var(--color10);
    background-color: var(--color30);
    position: relative;
}
.title
{
    margin: 20px auto;
    width: fit-content;
    color: var(--color10);
    font-weight: 900;
}
.input-container
{

    width: 400px;
    height: 320px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.reg-input
{

    font-size: 32px;
    font-family: inherit;
    display: block;
    width: 100%;
    border: none;
    outline: none;
    border-bottom: 1px solid var(--color10);
    border-radius: 10px;
    z-index: 10;
    background-color: var(--color30);
}
.reg-input:focus
{
    border-bottom: 4px solid var(--color10);
}
label
{
    display: block;
    width: fit-content;
    font-size: 20px;
    font-weight:100;
    overflow:visible;
    margin-bottom: -5px;
    color: #00000069
}
.Reg-form input.submit
{
    position: absolute;
    outline: none;
    border: none;
    background-color: var(--color30);
    right: 0;
    bottom: 0;
    margin: 0 40px 40px 0;
    color:var(--color10);
    font-weight: 900;
    font-family: inherit;
    font-size: 24px;
    cursor: pointer;
    width: 5em;
    height: 2em;
    box-shadow: 2px 2px 4px var(--color10);
   
   
}
.Reg-form input.submit:focus
{
    box-shadow: 0.5px 0.5px 2px var(--color10);
}
footer
{
   
    font-size: 20px;
    margin: -1em auto;
    bottom: 0;
    color: #00000065;  
} 
footer a
{
    text-decoration: none;
    color: var(--color10);
}